<template>
  <div id="app">
    <h1 class="game__title">Simon game</h1>
    <div class="game__container">
      <ul class="game-field__list" :class="{ disabled: !this.userStep }">
        <li class="game-field__item">
          <button
            class="game-field__btn btn btn__blue"
            :class="{ active: step == 1 }"
            @click="onUserClick(1)"
          ></button>
        </li>
        <li class="game-field__item">
          <button
            class="game-field__btn btn btn__red"
            :class="{ active: step == 2 }"
            @click="onUserClick(2)"
          ></button>
        </li>
        <li class="game-field__item">
          <button
            class="game-field__btn btn btn__yellow"
            :class="{ active: step == 3 }"
            @click="onUserClick(3)"
          ></button>
        </li>
        <li class="game-field__item">
          <button
            class="game-field__btn btn btn__green"
            :class="{ active: step == 4 }"
            @click="onUserClick(4)"
          ></button>
        </li>
      </ul>
      <form class="div game__control-panel">
        <h2 class="game__status">Игра: {{ getStatusGame }}</h2>
        <h3 class="game__step">Раунд: {{ getRoundCount }}</h3>
        <h3 v-if="!startBtnActive" class="game__step">
          Чей ход: {{ whoseMove }}
        </h3>
        <ul class="game-level__list" :class="{ disabled: !startBtnActive }">
          <li class="game-level__item">
            <label for="level-easy" class="game-level__label"
              >Легкий (1.5 сек.)</label
            >
            <input
              type="radio"
              class="game-level__checkbox"
              v-model="level"
              id="level-easy"
              value="level-easy"
            />
          </li>
          <li class="game-level__item">
            <label for="level-medium" class="game-level__label"
              >Средний (1 сек.)</label
            >
            <input
              type="radio"
              class="game-level__checkbox"
              v-model="level"
              id="level-medium"
              value="level-medium"
            />
          </li>
          <li class="game-level__item">
            <label for="level-hard" class="game-level__label"
              >Сложный (0.4 сек.)</label
            >
            <input
              type="radio"
              class="game-level__checkbox"
              v-model="level"
              id="level-hard"
              value="level-hard"
            />
          </li>
        </ul>
        <button
          v-if="startBtnActive"
          class="game__btn"
          @click="onStart"
          @submit.prevent
          type="button"
        >
          Старт
        </button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  components: {},
  data() {
    return {
      soundListGenerated: [],
      soundListUserClicked: [],
      autoStep: false,
      userStep: false,
      userMistake: false,
      step: 0,
      sound: {
        sound1: require("./assets/sounds/1.mp3"),
        sound2: require("./assets/sounds/2.mp3"),
        sound3: require("./assets/sounds/3.mp3"),
        sound4: require("./assets/sounds/4.mp3"),
      },
      level: "level-easy",
    };
  },
  methods: {
    onPlaySoundOne(item) {
      new Audio(this.sound[`sound${item}`]).play();
    },
    generateItemCount() {
      const itemCount = Math.ceil(Math.random() * 4);
      this.soundListGenerated.push(itemCount);
      this.soundListUserClicked = [];
      console.log(itemCount, this.soundListGenerated);
    },
    autoStepIteration() {
      let iterCount = 0;
      const speedGame = this.getSpeedGame;
      const speedOffBtn = this.getSpeedGame * 0.8;

      let timer = setInterval(() => {
        this.step = this.soundListGenerated[iterCount];
        this.onPlaySoundOne(this.step);

        iterCount++;
        if (iterCount > this.soundListGenerated.length) {
          this.step = 0;
          this.userStep = true;
          clearInterval(timer);
        }
        setTimeout(() => {
          this.step = 0;
        }, speedOffBtn);
      }, speedGame);

      timer;
    },
    gameOn() {
      this.userStep = false;
      this.generateItemCount();
      this.autoStepIteration();
    },
    onStart() {
      this.gameOn();
    },
    onUserClick(item) {
      const checkIndex = this.soundListUserClicked.length;

      this.onPlaySoundOne(item);
      if (item == this.soundListGenerated[checkIndex]) {
        this.soundListUserClicked.push(item);
        if (
          this.soundListGenerated.length === this.soundListUserClicked.length
        ) {
          this.gameOn();
        }
      } else {
        this.userMistake = true;
        this.userStep = false;
        this.soundListUserClicked = [];
        this.soundListGenerated = [];
      }
    },
  },
  computed: {
    startBtnActive() {
      return this.soundListGenerated.length === 0;
    },
    getRoundCount() {
      return this.soundListGenerated.length;
    },
    whoseMove() {
      return this.userStep ? "Игрока" : "Компьютера";
    },
    getSpeedGame() {
      if (this.level === "level-medium") {
        return 1000;
      }
      if (this.level === "level-hard") {
        return 400;
      }
      return 1500;
    },
    getStatusGame() {
      return this.userMistake
        ? 'Ошибка! Для повтора нажмите "Старт"!'
        : 'Нажмите "Старт"!';
    },
  },
};
</script>

